.loader {
  transform-origin: 0 0;
  background: #fff;
  width: 4px;
  height: 86px;
  margin: auto;
  animation: 1s linear infinite alternate shake, 1s linear infinite lightup;
  position: relative;
}

.loader:before {
  content: "";
  box-sizing: border-box;
  border: 5px solid #ff3d00;
  border-top: none;
  border-radius: 0 0 20px 20px;
  width: 32px;
  height: 16px;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
}

.loader:after {
  content: "";
  background: #fff;
  border-radius: 50px 50px 0 0;
  width: 64px;
  height: 32px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

@keyframes shake {
  0% {
    transform: rotate(10deg);
  }

  100% {
    transform: rotate(-10deg);
  }
}

@keyframes lightup {
  0%, 20%, 40% {
    opacity: 0;
  }

  10%, 30%, 50%, 100% {
    opacity: 1;
  }
}

